import { HIs, MyUI } from "@macashipo/mlib"
import React from "react"
import { KMyUITypeCommon } from "src/helpers/K"
import useConfigTableCell from "../../hooks/useConfigTableCell"
export default function MTCReadonlyHtml(props) {
  const { cell } = props
  const ctc = useConfigTableCell({ props })
  if (HIs.isObject(cell)) {
    return <div></div>
  }
  return (
    <div className="mct-readonly_html disabled" style={ctc.styleWrap()}>
      <MyUI type={KMyUITypeCommon.html} html={cell} data={ctc.row} />
    </div>
  )
}
